import { useCallback, useEffect } from 'react';

const useOnKeyDown = (
  keyCode: string,
  callback: () => void,
  condition: boolean,
) => {
  const keyFunction = useCallback((event : KeyboardEvent) => {
    if (event.code === keyCode) {
      callback();
    }
  }, [callback]);

  useEffect(() => {
    if (condition) {
      document.addEventListener('keydown', keyFunction);
    }

    return () => {
      document.removeEventListener('keydown', keyFunction);
    };
  }, [keyFunction, condition]);
};

export const useOnEnterDown = (
  callback: () => void,
  condition = true,
) => useOnKeyDown('Enter', callback, condition);